html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  font-size: 0;
  vertical-align: middle;
}

a {
  position: relative;
  transition: color 0.2s;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: currentColor;
    transition: width 0.2s;
  }

  &:hover,
  &:focus-visible {
    outline: 0;

    &::after {
      width: 100%;
    }
  }
}

button {
  cursor: pointer;
}

svg {
  transition: fill 0.2s;
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
